@font-face {
    font-family: 'Montserrat';
    src: url('./fonts/montserrat-regular.woff2') format('woff2'), url('./fonts/montserrat-regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

html {
    font-size: 16px;
    font-family: "Montserrat";
}

body {
    margin: 0;
}

.todo-items {
    display: flex;
    flex-direction: column;
}

.item-completed {
    text-decoration: line-through;
}